/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>Originally posted as a <a href=\"https://gist.github.com/adamstegman/5077010\">gist</a> in response to <a href=\"https://twitter.com/adamstegman/status/307944394445688832\">this twitter conversation</a>.</p>\n<p>I'm working on a Rails application that uses the <a href=\"http://archive.msdn.microsoft.com/ewsjavaapi\">Exchange Webservices API</a>. Unfortunately the EWS API has a weird package, but I thought that wouldn't be hard to overcome. So I wrote this script:</p>\n<pre><code>require 'java'\nrequire 'vendor/lib/EWSJavaAPI_1.2.jar'\n\ndef microsoft\n  Java::Microsoft\nend\n\nversion = microsoft.exchange.webservices.data.ExchangeVersion::Exchange2010_SP1\nservice = microsoft.exchange.webservices.data.ExchangeService.new(version)\n</code></pre>\n<p>Getting the ExchangeVersion enum worked fine, but trying to access the ExchangeService class resulted in this error:</p>\n<blockquote>\n<pre><code>NameError: missing class or uppercase package name (`microsoft.exchange.webservices.data.ExchangeService')\nget_proxy_or_package_under_package at org/jruby/javasupport/JavaUtilities.java:54\nmethod_missing at file:/Users/adam/.rvm/rubies/jruby-1.7.3/lib/jruby.jar!/jruby/java/java_package_module_template.rb:14\n(root) at lib/sandbox.rb:11\n</code></pre>\n</blockquote>\n<p>I thought the problem was with the package name, but the error is actually that the EWS API is missing its dependencies. It needed commons-httpclient and commons-logging-api on the classpath. I discovered this via clojure's error message for similar code:</p>\n<pre><code>(import '(microsoft.exchange.webservices.data ExchangeService))\n</code></pre>\n<blockquote>\n<pre><code>ClassNotFoundException org.apache.commons.httpclient.HttpConnectionManager  java.net.URLClassLoader$1.run (URLClassLoader.java:366)\n</code></pre>\n</blockquote>\n<p>So the real issue here is JRuby's error message. Interestingly enough, JRuby did give me a better error once commons-httpclient was on the classpath:</p>\n<blockquote>\n<pre><code>MultiThreadedHttpConnectionManager.java:70:in `&#x3C;clinit>': java.lang.NoClassDefFoundError: org/apache/commons/logging/LogFactory\n</code></pre>\n</blockquote>\n<p>That told me commons-logging-api was also missing. JRuby should have told me that in the first place.</p>","fields":{"post":{"title":"JRuby NameError: missing class or uppercase package name","link":null,"timestamp":"2013-04-13T19:43Z","date":"April 13, 2013 7:43pm UTC"}}}},"pathContext":{"slug":"/jruby-nameerror-missing-class-or-uppercase-package-name.html"}}

/***/ })

